body {
  background-color: #fff;
  line-height: 1.4;
  font-family: 'RobotoCondensed', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #262533;
  min-width: 320px;
  margin: 0;
  padding: 0;
  border: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::selection {
  background-color: #e72727;
  color: #fff;
}

a {
  text-decoration: none;
  color: currentColor;
}

a:hover {
  text-decoration: none;
}

.inner {
  max-width: 1140px;
  padding: 0 30px;
  margin: 0 auto;
}

.section {
  padding-top: 100px;
}

.bg-blue {
  background-color: #262533;
}

.t-center {
  text-align: center;
}

.header__top {
  background-color: #262533;
  padding: 10px 0;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header__list {
  display: flex;
  align-items: center;
  margin: 0 -10px;
}

.header__list li {
  display: flex;
  align-items: center;
  font-size: 14px;
}

.header__list li:not(:last-child) {
  margin-right: 15px;
}

.no-touch .header__list li:hover a {
  color: #e72727;
}

.header__list a {
  color: #fff;
  transition: color .5s;
}

.header__list .icon {
  color: #e72727;
  margin: 0 10px;
}

.header__main {
  background-color: #f8f8f8;
  padding: 10px 0;
}

.header.is-active .header__main {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  box-shadow: 0px 3px 16px 0px rgba(0, 0, 0, 0.1);
  animation: header .5s forwards;
}

.header__logo {
  max-width: 110px;
}

@keyframes header {
  0% {
    transform: translateY(-80px);
  }
  100% {
    transform: translateY(0px);
  }
}

.logo {
  flex-shrink: 0;
}

.logo a {
  display: block;
}

.logo img {
  display: block;
  max-width: 100%;
}

.nav__list {
  display: flex;
  align-items: center;
  text-transform: uppercase;
}

.nav__item {
  font-size: 14px;
  font-weight: 700;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav a {
  position: relative;
  padding: 2px 8px;
  display: inline-block;
  transition: color .5s;
}

.nav a:before {
  content: '';
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform .5s;
}

.no-touch .nav a:hover {
  color: #e72727;
}

.no-touch .nav a:hover:before {
  transform: scaleX(1);
  transform-origin: left;
}

.header.is-active + .hero {
  padding-top: 72px;
}

.hero__slider {
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s, visibility .3s;
}

.hero__slider.slick-initialized {
  white-space: normal;
  overflow: visible;
  opacity: 1;
  visibility: visible;
}

.hero__bg {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  height: calc(100vh - 111px);
  position: relative;
}

.hero__bg:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero__inner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
}

.hero__content {
  z-index: 1;
  position: relative;
  display: flex;
  text-align: right;
}

.hero__main {
  padding: 40px;
  background-color: #262533;
  color: #fff;
}

.hero__aside {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.hero__title span {
  display: block;
}

.hero__subtitle {
  padding: 0 20px;
}

.dots {
  height: 8px;
  width: 8px;
  border-radius: 50%;
  background-color: #262533;
  transition: background-color .3s, transform .3s;
  position: relative;
}

.dots:after {
  content: '';
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200%;
  height: 200%;
}

.slick-active .dots {
  background-color: #e72727;
  transform: scale(1.8);
}

.small-dots {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 40px;
}

.small-dots li {
  line-height: 0;
}

.small-dots li:not(:first-child) {
  margin-left: 15px;
}

@media only screen and (max-width: 429px) {
  .small-dots li:not(:first-child) {
    margin-left: 6px;
  }
}

.features__inner {
  display: flex;
}

.features__left {
  flex: 0 0 60%;
  min-width: 60%;
}

.features__right {
  flex: 0 0 40%;
  min-width: 40%;
}

.features__list {
  display: flex;
  flex-wrap: wrap;
  margin: -30px 0 -30px -15px;
}

.features__item {
  width: 50%;
  padding: 30px 15px;
}

.features__title {
  padding-bottom: 30px;
}

.features__btn {
  padding-top: 40px;
}

.feature {
  transition: border-color .5s;
}

.partners__right .feature {
  width: calc(50% - 30px);
  margin: 30px 15px 0 15px;
  padding-bottom: 10px;
  border-bottom: 3px solid #e4e7e9;
}

.no-touch .feature:hover {
  border-color: #e72727;
}

.no-touch .feature:hover .feature__link {
  padding-left: 60px;
}

.no-touch .feature:hover .feature__link:before {
  width: 40px;
}

.feature__icon {
  font-size: 80px;
  line-height: 1;
  color: #e72727;
}

.feature__title {
  padding-top: 10px;
  padding-bottom: 15px;
}

.feature__link {
  display: inline-block;
  position: relative;
  margin-top: 15px;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 700;
  transition: padding-left .5s;
}

.feature__link:before {
  content: '';
  position: absolute;
  left: 0px;
  top: 50%;
  margin-top: -2px;
  width: 0px;
  height: 3px;
  background: #e72727;
  transition: width .5s;
}

.partners {
  padding-bottom: 100px;
}

.partners__inner {
  display: flex;
  justify-content: space-between;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.partners__left {
  flex: 0 0 60%;
  min-width: 60%;
}

.partners__right {
  flex: 0 0 40%;
  min-width: 40%;
  padding-left: 20px;
  display: flex;
  flex-wrap: wrap;
}

.partners__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 80px;
  margin: -20px;
}

.partners__item {
  width: calc(100%/3 - 40px);
  margin: 20px;
  height: 77px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 50%;
  filter: grayscale(100%);
  transition: filter .6s, box-shadow .6s;
}

.no-touch .partners__item:hover {
  filter: grayscale(0);
  box-shadow: 0px 10px 40px -14px #e72727;
}

.services {
  padding-bottom: 80px;
}

.services__title {
  max-width: 500px;
  margin: 0 auto;
}

.services__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -20px;
  padding-top: 60px;
}

.services__item {
  width: 50%;
  padding: 20px;
}

.service {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.no-touch .service:hover .service__img {
  transform: scale(1.1);
}

.no-touch .service:hover .service__content {
  background-color: #fff;
}

.no-touch .service:hover .service__title {
  color: #e72727;
}

.service__img {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  height: 0;
  padding-bottom: 40%;
  transition: transform .5s;
}

.service__img-wrap {
  overflow: hidden;
}

.service__content {
  flex-grow: 1;
  padding: 30px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-top: none;
  transition: background-color .5s;
}

.service__title {
  color: #fff;
  padding-bottom: 30px;
  transition: color .5s;
}

.testimonials {
  padding: 150px 0;
}

.testimonials__slider-wrap {
  margin-top: 80px;
  position: relative;
}

.testimonials__slider .slick-list {
  max-width: 730px;
  margin: 0 auto;
  box-shadow: 0px 40px 91px -37px rgba(231, 39, 39, 0.5);
}

.testimonial {
  background-color: #e72727;
  color: #fff;
  padding: 70px 100px;
}

.testimonial blockquote {
  font-style: italic;
}

.testimonial p {
  color: #fff;
}

.testimonial__person {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
}

.testimonial__img {
  height: 60px;
  width: 60px;
  border-radius: 50%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  margin-right: 24px;
}

.testimonial__info p {
  font-size: 14px;
}

.arrow-prev, .arrow-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
}

.arrow-prev button, .arrow-next button {
  display: inline-flex;
  color: #fff;
  padding: 17px;
  font-size: 16px;
  text-align: center;
  position: relative;
  background-color: #e72727;
  transition: background-color .5s;
}

.no-touch .arrow-prev button:hover, .no-touch .arrow-next button:hover {
  background-color: #262533;
}

.arrow-prev {
  left: 0;
}

.arrow-prev button:after {
  content: '';
  position: absolute;
  bottom: 5px;
  left: 5px;
  border-left: 8px solid #fff;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #fff;
  border-top: 8px solid transparent;
}

.arrow-next {
  right: 0;
}

.arrow-next button:after {
  content: '';
  position: absolute;
  bottom: 5px;
  right: 5px;
  border-right: 8px solid #fff;
  border-left: 8px solid transparent;
  border-bottom: 8px solid #fff;
  border-top: 8px solid transparent;
}

.contact {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 58px 68px;
  background-color: #e72727;
  box-shadow: 0px 40px 91px -37px rgba(231, 39, 39, 0.5);
  position: relative;
  z-index: 1;
}

.contact__content .subtitle {
  padding-bottom: 15px;
}

.contact__content .title {
  line-height: 1;
  font-weight: 400;
}

.footer {
  background-color: #262533;
  color: #8f8f8f;
  padding-top: 150px;
  position: relative;
}

.footer:before {
  content: '';
  position: absolute;
  z-index: -1;
  top: -95px;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #262533;
}

.footer .icon {
  color: #e72727;
  margin-right: 7px;
}

.footer__list {
  display: flex;
  margin: 0 -15px;
  padding-bottom: 100px;
}

.footer__item {
  width: calc(33.3% - 30px);
  margin: 0 15px;
}

.footer__title {
  text-transform: uppercase;
