}

.footer__title:before {
  right: 0;
  top: 0;
  border-right: 3px solid #e72727;
  border-top: 3px solid #e72727;
}

.footer__title:after {
  left: 0;
  bottom: 0;
  border-left: 3px solid #e72727;
  border-bottom: 3px solid #e72727;
}

.footer__address {
  display: flex;
  text-align: center;
  max-width: 220px;
  margin: 0 auto;
}

.footer__address .icon {
  flex-shrink: 0;
  margin-top: 3px;
}

.footer__contacts {
  display: flex;
  flex-direction: column;
}

.footer__contacts-item {
  display: flex;
  align-items: center;
  align-self: center;
  transition: color .3s;
}

.footer__contacts-item:not(:first-child) {
  padding-top: 5px;
}

.footer__contacts-item:first-child {
  align-self: flex-start;
}

.footer__contacts-item:last-child {
  align-self: flex-end;
}

.no-touch .footer__contacts-item:hover {
  color: #fff;
}

.footer__copy {
  position: relative;
  text-align: right;
}

.footer__copy:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #8f8f8f;
  top: 0;
  left: 0;
}

.footer__copy span {
  font-size: 14px;
  text-transform: uppercase;
}

.btn {
  display: inline-block;
  border: none;
  color: #fff;
  text-decoration: none;
  padding: 18px 36px;
  font-size: 13px;
  font-family: 'RobotoCondensed', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  position: relative;
  outline: none;
  cursor: pointer;
  transition: background-color .5s, color .5s;
  z-index: 1;
}

.btn:before {
  content: '';
  position: absolute;
  top: 5px;
  right: 5px;
  bottom: 5px;
  left: 5px;
  border: 1px solid #fff;
  transition: background-color .5s;
  z-index: -1;
}

.btn:after {
  content: '';
  position: absolute;
  bottom: 5px;
  right: 5px;
  border-right: 10px solid #fff;
  border-left: 10px solid transparent;
  border-bottom: 10px solid #fff;
  border-top: 10px solid transparent;
  transition: border-color .5s;
}

.btn_blue {
  background-color: #262533;
}

.no-touch .btn_blue:hover {
