}

.no-touch .btn_red:hover:before {
  background-color: #fff;
}

.no-touch .btn_red:hover:after {
  border-right-color: #e72727;
  border-bottom-color: #e72727;
}

.btn_white {
  background-color: #fff;
  color: #262533;
}

.no-touch .btn_white:hover {
  color: #fff;
}

.no-touch .btn_white:hover:before {
  background-color: #e72727;
}

.no-touch .btn_white:hover:after {
  border-right-color: #fff;
  border-bottom-color: #fff;
}

.btn_white:after {
  border-right-color: #e72727;
  border-bottom-color: #e72727;
}

