//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }


$familyRoboto: 'RobotoCondensed';

@mixin roboto {
	font-family: 'RobotoCondensed', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif; }


//ROBOTO

@font-face {
	@include fontdefault(normal, 400, $familyRoboto);
	@include fontpath('RobotoCondensed-Regular', 'RobotoCondensed-Regular', RobotoCondensed-Regular); }
@font-face {
	@include fontdefault(italic, 400, $familyRoboto);
	@include fontpath('RobotoCondensed-Italic', 'RobotoCondensed-Italic', RobotoCondensed-Italic); }

@font-face {
	@include fontdefault(normal, 200, $familyRoboto);
	@include fontpath('RobotoCondensed-Light', 'RobotoCondensed-Light', RobotoCondensed-Light); }
@font-face {
	@include fontdefault(italic, 200, $familyRoboto);
	@include fontpath('RobotoCondensed-LightItalic', 'RobotoCondensed-LightItalic', RobotoCondensed-LightItalic); }

@font-face {
	@include fontdefault(normal, 700, $familyRoboto);
	@include fontpath('RobotoCondensed-Bold', 'RobotoCondensed-Bold', RobotoCondensed-Bold); }
@font-face {
	@include fontdefault(italic, 700, $familyRoboto);
	@include fontpath('RobotoCondensed-BoldItalic', 'RobotoCondensed-BoldItalic', RobotoCondensed-BoldItalic); }
